import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { Button } from '@material-ui/core';
import { titleChanged } from './store/view';
import { resetRecords, removeRecordIndex } from './store/records'
import {
  outputContainer,
  topButton
} from './utils/styles';
import {
  Autorenew as AutorenewIcon,
  GetApp as DownloadIcon
} from '@material-ui/icons'
import { langs } from './lang/langs';
import { Label, ReferenceLine, CartesianGrid, XAxis, YAxis, Tooltip, Legend, Line, Bar, ComposedChart, ResponsiveContainer } from 'recharts';

export const Records = () => {
  const state = useSelector(state => state.records)
  const settings = useSelector(state => state.settings)
  const lang = langs[settings.lang]
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(
      titleChanged(lang.recordsTitle)
    )
  })

  const removeRecord = (event) => {
    const index = parseInt(event.target.getAttribute('data-index'))
    dispatch(removeRecordIndex(index))
  }

  const downloadData = state => {
    const a = document.createElement('a')
    a.setAttribute('download', `${new Date().toISOString()}_records.json`)
    const json = window.btoa(JSON.stringify(state, null, 2))
    a.setAttribute('href', `data:text/json;base64,${json}`)
    a.click()
  }

  const getIntensity = (time, value, halflifeHHmm) => {
    if (!halflifeHHmm) return 0
    if (halflifeHHmm.length === 1) halflifeHHmm = `00:0${halflifeHHmm}`
    if (halflifeHHmm.length === 2) halflifeHHmm = `00:${halflifeHHmm}`
    if (halflifeHHmm.length === 4) halflifeHHmm = `0${halflifeHHmm}`
    const halfLifeMs = Date.parse('1970-01-01T' + halflifeHHmm + 'Z'); // time it peaks and then interval it halves
    const now =  Date.now();
    const gap = now - time;
    const passedIntervals = (gap / halfLifeMs)
    if (passedIntervals > 10) return 0
    const ratio = Math.pow(2, passedIntervals)
    const intensity = 1 / ratio
    if (value * intensity < 1) return 0
    return intensity
  }

  const now = Date.now()
  const oneHour = 60 * 60 * 1000
  const endDate = now

  const midnight = ((Math.floor(now / (oneHour * 24)) * (oneHour * 24)) - endDate) / oneHour

  const data = state.map(row => {
    return {
      date: row.data,
      hour: (row.date - endDate) / oneHour,
      units: row.roundedAdjustedTotalUnits,
      carbs: row.consumedCarbs,
      glucose: row.currentBg
    }
  }).filter(row => row.hour > -24)

  return (
    <React.Fragment>
    <section className="insulinunits screen__container">

      <div className="insulinunits__output" style={outputContainer}>
        <ResponsiveContainer width={"100%"} height={300}>
        <ComposedChart
            data={data}
            margin={{
              top: 2,
              right: 2,
              bottom: 2,
              left: 2
            }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="hour" type="number" domain={[-24, 0]} interval={-2}>
              <Label value="last 24 hours" offset={-6} position="bottom" />
            </XAxis>
          <YAxis yAxisId='left'>
            <Label value="carbs" angle={-90} offset={6} position="center" />
          </YAxis>
          <YAxis yAxisId="right" orientation='right'>
            <Label value="units and glucose" angle={90} offset={6} position="center" />
          </YAxis>
          <Tooltip labelFormatter={(value, name, props) => {
            return new Date(endDate+(value*oneHour)).toLocaleDateString("en-GB", { weekday: 'short', day: 'numeric', month: 'short', hour: 'numeric', minute: 'numeric' })
          }}/>
          <Legend />
          <ReferenceLine yAxisId="left" x={String(midnight)} stroke="grey" label="" />
          <Bar yAxisId="left" dataKey="carbs" fill="green" />
          <Line yAxisId="right" type="monotone" dataKey="glucose" stroke="blue" />``
          <Bar yAxisId="right" dataKey="units" fill="red" />
        </ComposedChart>
        </ResponsiveContainer>
      </div>

      <div className="insulinunits__button__reset" style={{ display: 'flex'}}>
        <Button
          className='button'
          style={topButton}
          onClick={
            () => {
              dispatch(resetRecords(state))
            }
          }
        >
          <AutorenewIcon />
          &nbsp;{lang.recordsResetButtonText}
        </Button>
        <Button
          className='button'
          style={topButton}
          onClick={
            () => {
              downloadData(state)
            }
          }
        >
          <DownloadIcon />
          &nbsp;{lang.recordsDownloadButtonText}
        </Button>
      </div>

      <div className="insulinunits_input input__group">
          <table>
            <thead>
            <tr>
              <th>Date</th>
              <th>Glucose</th>
              <th>Carbs</th>
              <th>Units</th>
              <th>For</th>
              <th></th>
            </tr>
            </thead>
            {state.map((row, index) => {
              const unitsBackgroundColor =
                !row.roundedAdjustedTotalUnits
                  ? 'transparent'
                  : `rgba(255,0,0, ${getIntensity(row.date, row.roundedAdjustedTotalUnits, settings.basalHalfLife)})`
              const carbsBackgroundColor =
                !row.consumedCarbs
                  ? 'transparent'
                  : `rgba(0,128,0, ${getIntensity(row.date, row.consumedCarbs, settings.carbsHalfLife)})`
              return <tr key={index}>
                <td>{new Date(row.date).toLocaleDateString("en-GB", { weekday: 'short', day: 'numeric', month: 'short', hour: 'numeric', minute: 'numeric' })}</td>
                <td>{row.currentBg}mmol/l</td>
                <td style={{backgroundColor: carbsBackgroundColor}}>{row.consumedCarbs}g</td>
                <td style={{ backgroundColor: unitsBackgroundColor }}>{row.calculationType === null ? 'none' : row.roundedAdjustedTotalUnits}</td>
                <td>{row.calculationType === null ? 'n/a' : row.calculationType}</td>
                <td><Button style={{minWidth: '12px'}} onClick={removeRecord} data-index={index}>x</Button></td>
              </tr>
            })}
          </table>

      </div>

    </section>
  </React.Fragment>
  )
}
