const version = "0.0.31";

function addUI(settings) {
  if (!settings) return settings;
  const output = {};
  for (const key in settings) {
    if (key.startsWith('ui_')) continue;
    output[key] = settings[key]
    output[`ui_${key}`] = settings[key]
  }
  return output
}

function removeVersion(settings) {
  return {...settings, version: null}
}

export const defaultSettings = addUI({
  version,
  needsUpdating: false,
  calculationType: 'both',
  consumedCarbs: 0,
  currentBg: 5,
  minimumUnits: 0.1,
  basalHalfLife: "",
  carbsHalfLife: "00:30",
  carbsPerUnit: 0,
  bgPerUnit: 0,
  targetBg: 5,
  unitAdjustmentPercentage: 0,
  carbsPerColumnAmount: 0,
  columnAmount: 100,
  totalPacketAmount: 100,
  portionRatio: 1,
  minimumCarbs: 0.1,
  lang: 'en'
});

export const defaultInsulinUnits = {
  ui_calculationType: null,
  consumedCarbs: null,
  currentBg: null,
  unitAdjustmentPercentage: null,
  minimumUnits: null,
  carbsPerUnit: null,
  bgPerUnit: null,
  targetBg: null,
  basalHalfLife: null,
  carbsHalfLife: null,
  ui_carbsHalfLife: null,
  ui_basalHalfLife: null,
  ui_consumedCarbs: null,
  ui_currentBg: null,
  ui_unitAdjustmentPercentage: null,
  ui_minimumUnits: null,
  ui_carbsPerUnit: null,
  ui_bgPerUnit: null,
  ui_targetBg: null,
  ui_unitAdjustmentRatio: 0,
  ui_isTaken: false,
  bgDistance: 0,
  bgUnits: 0,
  carbUnits: 0,
  totalUnits: 0,
  adjustedTotalUnits: 0,
  roundedAdjustedTotalUnits: 0
}

const savedSettings = removeVersion(addUI(JSON.parse(localStorage.getItem('settings') || 'null')) || {});
for (const key in defaultSettings) {
  savedSettings[key] = savedSettings?.[key] ?? defaultSettings[key];
}
const savedInsulinUnits = JSON.parse(localStorage.getItem('insulinUnits') || '{}')
const restoredInsulinUnits = {}
for (const key in defaultInsulinUnits) {
  restoredInsulinUnits[key] = savedInsulinUnits?.[key] ?? defaultInsulinUnits[key];
}
const savedRecords = JSON.parse(localStorage.getItem('records') || '[]');

export const initialState = {
  view: {
    title: ''
  },
  settings: savedSettings,
  insulinUnits: restoredInsulinUnits,
  proportionateCarbs: {
    carbsPerColumnAmount: null,
    columnAmount: null,
    totalPacketAmount: null,
    portionRatio: null,
    minimumCarbs: null,
    ui_carbsPerColumnAmount: null,
    ui_columnAmount: null,
    ui_totalPacketAmount: null,
    ui_portionRatio: null,
    ui_minimumCarbs: null,
    ui_isTaken: false,
    carbRatio: 0,
    totalPacketCarbs: 0,
    consumedCarbs: 0,
    roundedConsumedCarbs: 0
  },
  records: savedRecords
}
