import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import {
  Link
} from "react-router-dom";
import { Typography, TextField, InputAdornment, Button, Select, InputLabel } from '@material-ui/core'
import Alert from '@material-ui/lab/Alert';
import { titleChanged } from './store/view';
import { allInsulinUnitsChanged } from './store/insulinUnits';
import { allProportionateCarbsChanged } from './store/proportionateCarbs'
import mergeStates from './utils/mergeStates'
import makeOnFocusSelect from './utils/makeOnFocusSelect';
import makeOnChanged from './utils/makeOnChanged';
import makeOnBlur from './utils/makeOnBlur'
import {
  outputContainer,
  topButton
} from './utils/styles';
import {
  ArrowBack as CloseIcon,
  Maximize as MaximizeIcon,
  MenuOpen as MenuOpenIcon,
  Cake as CakeIcon,
  DataUsage as DataUsageIcon,
  Add as AddIcon,
  Autorenew as AutorenewIcon,
  Info as InfoIcon,
} from '@material-ui/icons'
import resetStates from './utils/resetStates';
import { langs } from './lang/langs';

export const ProportionateCarbs = () => {
  const insulinUnitsState = useSelector(state => state.insulinUnits)
  const state = useSelector(state => state.proportionateCarbs)
  const settings = useSelector(state => state.settings)
  const lang = langs[settings.lang]
  const values = mergeStates(state, settings)
  const dispatch = useDispatch()

  const onKeyUp = (event) => {
    if (event.keyCode !== 13) return;
    event.target.blur()
  }
  const onFocusSelect = makeOnFocusSelect()
  const onChanged = makeOnChanged(state, settings, dispatch, allProportionateCarbsChanged)
  const onBlur = makeOnBlur(settings, dispatch, allProportionateCarbsChanged)

  useEffect(() => {
    dispatch(
      titleChanged(lang.proportionateCarbsTitle)
    )
  })

  const [showCarbRatio, setShowCarbRatio] = useState(false)
  const [showCarbsPerAmount, setShowCarbsPerAmount] = useState(false)
  const [showColumnAmount, setShowColumnAmount] = useState(false)
  const [showAmountConsumed, setShowAmountConsumed] = useState(false)
  const [showTotalAmount, setShowTotalAmount] = useState(false)
  const [showPortionRatio, setShowPortionRatio] = useState(false)

  return (
    <section className="proportionatecarbs screen__container">

      <div className="proportionatecarbs__output" style={outputContainer}>
        <Typography variant="h2" noWrap>
          {state.roundedConsumedCarbs||0}{lang.proportionateCarbsUnitText}
        </Typography>
      </div>

      <div className="proportionatecarbs__button__addtoinsulin" style={{display:'flex'}}>

        <Button
          className='button'
          style={topButton}
          component={Link} to="/insulinunits"
        >
          <CloseIcon />
          &nbsp;{lang.proportionateCarbCancelButtonText}
        </Button>
        <Button
          className='button'
          style={topButton}
          component={Link} to="/insulinunits"
          disabled={insulinUnitsState.ui_isTaken || state.roundedConsumedCarbs === 0}
          onClick={
            () => {
              const carbs = (state.roundedConsumedCarbs||0);
              if (!carbs) return;
              const value = parseFloat(insulinUnitsState.ui_consumedCarbs)
                ? `${insulinUnitsState.ui_consumedCarbs}+${carbs}`
                : `${carbs}`
              dispatch(allInsulinUnitsChanged(mergeStates(
                insulinUnitsState,
                settings,
                {
                  'consumedCarbs': value,
                  'ui_consumedCarbs': value
                }
              )))
            }
          }
        >
          <AddIcon />
          &nbsp;{lang.proportionateCarbsAddButtonText}
        </Button>
      </div>

      <div className="proportionatecarbs__button__reset">
        <Button
          className='button'
          disabled={insulinUnitsState.ui_isTaken}
          style={topButton}
          onClick={
            () => {
              dispatch(allProportionateCarbsChanged(resetStates(
                state,
                settings
              )))
            }
          }
        >
          <AutorenewIcon />
          &nbsp;{lang.proportionateCarbsResetButtonText}
        </Button>
      </div>

      <Typography className="section__divider" variant="h6" noWrap>
        {lang.proportionateCarbsCarbRatioTitle}
        {lang.proportionateCarbsCarbRatioInfoText &&
          <React.Fragment>
            <Button className="btn-info" onClick={() => setShowCarbRatio(!showCarbRatio)}>
              <InfoIcon />
            </Button>
            <Alert severity="info" className="info" style={{display: showCarbRatio ? 'flex':'none'}} onClose={() => setShowCarbRatio(false)}>
              {lang.proportionateCarbsCarbRatioInfoText}
            </Alert>
          </React.Fragment>
        }
      </Typography>

      <div className="proportionatecarbs_input input__group">

        <div className="proportionatecarbs__input__carbsperweight__container">
          <div className="proportionatecarbs__input__carbspercolumnweight input__container">
            <TextField label={<div>
            {lang.proportionateCarbsCarbsPerAmountLabelText}
            {lang.proportionateCarbsCarbsPerAmountInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowCarbsPerAmount(!showCarbsPerAmount)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" className="info" style={{display: showCarbsPerAmount ? 'flex':'none'}} onClose={() => setShowCarbsPerAmount(false)}>
                {lang.proportionateCarbsCarbsPerAmountInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
              disabled={insulinUnitsState.ui_isTaken}
              onKeyUp={onKeyUp}
              onFocus={onFocusSelect.bind(this, 'carbsPerColumnAmount')}
              onChange={onChanged.bind(this, 'carbsPerColumnAmount')}
              onBlur={onBlur.bind(this, 'carbsPerColumnAmount')}
              value={values.ui_carbsPerColumnAmount}
              InputProps={{
                startAdornment: <InputAdornment position="start"><MenuOpenIcon /></InputAdornment>,
                endAdornment: <InputAdornment position="end">{lang.proportionateCarbsCarbsPerAmountUnitText}</InputAdornment>,
              }}
            />
          </div>

          <div className="proportionatecarbs__input__columnweight input__container">
            <TextField label={<div>
            {lang.proportionateCarbsColumnAmountLabelText}
            {lang.proportionateCarbsColumnAmountInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowColumnAmount(!showColumnAmount)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" className="info" style={{display: showColumnAmount ? 'flex':'none'}} onClose={() => setShowColumnAmount(false)}>
                {lang.proportionateCarbsColumnAmountInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
              disabled={insulinUnitsState.ui_isTaken}
              onKeyUp={onKeyUp}
              onFocus={onFocusSelect.bind(this, 'columnAmount')}
              onChange={onChanged.bind(this, 'columnAmount')}
              onBlur={onBlur.bind(this, 'columnAmount')}
              value={values.ui_columnAmount}
              InputProps={{
                startAdornment: <InputAdornment position="start"><MaximizeIcon /></InputAdornment>,
                endAdornment: <InputAdornment position="end">{lang.proportionateCarbsColumnAmountUnitText}</InputAdornment>,
              }}
            />
          </div>
        </div>

        <Typography className="section__divider" variant="h6" noWrap>
          {lang.proportionateCarbsAmountConsumedTitle}
          {lang.proportionateCarbsAmountConsumedInfoText &&
          <React.Fragment>
            <Button className="btn-info" onClick={() => setShowAmountConsumed(!showAmountConsumed)}>
              <InfoIcon />
            </Button>
            <Alert severity="info" className="info" style={{display: showAmountConsumed ? 'flex':'none'}} onClose={() => setShowAmountConsumed(false)}>
              {lang.proportionateCarbsAmountConsumedInfoText}
            </Alert>
          </React.Fragment>
        }
        </Typography>

        <div className="proportionatecarbs__input__amountconsumed__container">
        <div className="proportionatecarbs__input__totalpacketweight input__container">
          <TextField label={<div>
            {lang.proportionateCarbsTotalAmountLabelText}
            {lang.proportionateCarbsTotalAmountInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowTotalAmount(!showTotalAmount)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" className="info" style={{display: showTotalAmount ? 'flex':'none'}} onClose={() => setShowTotalAmount(false)}>
                {lang.proportionateCarbsTotalAmountInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
            disabled={insulinUnitsState.ui_isTaken}
            onKeyUp={onKeyUp}
            onFocus={onFocusSelect.bind(this, 'totalPacketAmount')}
            onChange={onChanged.bind(this, 'totalPacketAmount')}
            onBlur={onBlur.bind(this, 'totalPacketAmount')}
            value={values.ui_totalPacketAmount}
            InputProps={{
              startAdornment: <InputAdornment position="start"><CakeIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">{lang.proportionateCarbsTotalAmountUnitText}</InputAdornment>,
            }}
          />
        </div>
        <div className="proportionatecarbs__input__portionratio input__container">
          <InputLabel htmlFor="portionratio">
            {lang.proportionateCarbsPortionRatioLabelText}
            {lang.proportionateCarbsPortionRatioInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowPortionRatio(!showPortionRatio)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" className="info" style={{display: showPortionRatio ? 'flex':'none'}} onClose={() => setShowPortionRatio(false)}>
                {lang.proportionateCarbsPortionRatioInfoText}
              </Alert>
            </React.Fragment>
            }
            </InputLabel>
          <div className="select__container">
          <Select
            native
            disabled={insulinUnitsState.ui_isTaken}
            value={values.ui_portionRatio}
            onChange={onChanged.bind(this, 'portionRatio')}
            onBlur={onBlur.bind(this, 'portionRatio')}
            label={lang.proportionateCarbsPortionRatioLabelText}
            inputProps={{
              name: 'portionratio',
              id: 'portionratio',
            }}
            startAdornment={<InputAdornment position="start"><DataUsageIcon /></InputAdornment>}
          >
            {lang.proportionateCarbsPortionOptions}
          </Select>
          </div>
        </div>
        </div>
      </div>

    </section>
  )
}
