import { createSlice } from '@reduxjs/toolkit'
import round from '../utils/round'
import min from '../utils/min'
import { initialState } from './initialState'

const doUnitMath = (state, stateUpdate) => {
  Object.assign(state, stateUpdate)

  const calculationType = state.ui_calculationType
  const isCarb = (calculationType === 'both' || calculationType === 'carbs')
  const isCorrection = (calculationType === 'both' || calculationType === 'correction')

  const consumedCarbs = state.consumedCarbs
  const currentBg = state.currentBg
  const unitAdjustmentPercentage = state.unitAdjustmentPercentage
  const targetBg = state.targetBg
  const carbsPerUnit = state.carbsPerUnit
  const bgPerUnit = state.bgPerUnit
  const minimumUnits = state.minimumUnits

  const unitAdjustmentRatio = 1 + (unitAdjustmentPercentage / 100)
  const bgDistance = (currentBg - targetBg)
  const bgUnits = bgPerUnit ? (bgDistance / bgPerUnit) : 0
  const carbUnits = carbsPerUnit ? (consumedCarbs / carbsPerUnit) : 0

  const totalUnits = [
    isCarb && carbUnits,
    isCorrection && bgUnits
  ].filter(Boolean).reduce((sum, value) => sum + value, 0)
  const adjustedTotalUnits = (totalUnits * unitAdjustmentRatio)
  const roundedAdjustedTotalUnits = min(round(adjustedTotalUnits, minimumUnits))

  Object.assign(state, {
    unitAdjustmentRatio,
    bgDistance,
    bgUnits,
    carbUnits,
    totalUnits,
    adjustedTotalUnits,
    roundedAdjustedTotalUnits
  })

}

const insulinUnitsSlice = createSlice({
  name: 'insulinUnits',
  initialState: initialState.insulinUnits,
  reducers: {
    allInsulinUnitsChanged(state, action) {
      doUnitMath(state, action.payload)
      localStorage.setItem('insulinUnits', JSON.stringify(state, null, 2))
    },
    markTaken(state, action) {
      state.ui_isTaken = true
      localStorage.setItem('insulinUnits', JSON.stringify(state, null, 2))
    }
  }
})

export const {
  allInsulinUnitsChanged,
  markTaken
} = insulinUnitsSlice.actions

export default insulinUnitsSlice.reducer
