import { createSlice } from '@reduxjs/toolkit'
import { initialState } from './initialState'

const recordsSlice = createSlice({
  name: 'records',
  initialState: initialState.records,
  reducers: {
    recordTakeInsulin(state, action) {
      const {
        ui_calculationType,
        consumedCarbs,
        currentBg,
        unitAdjustmentPercentage,
        minimumUnits,
        carbsPerUnit,
        bgPerUnit,
        targetBg,
        unitAdjustmentRatio,
        bgDistance,
        bgUnits,
        carbUnits,
        totalUnits,
        adjustedTotalUnits,
        roundedAdjustedTotalUnits
      } = action.payload
      state.unshift({
        date: Date.now(),
        calculationType: ui_calculationType,
        consumedCarbs,
        currentBg,
        unitAdjustmentPercentage,
        minimumUnits,
        carbsPerUnit,
        bgPerUnit,
        targetBg,
        unitAdjustmentRatio,
        bgDistance,
        bgUnits,
        carbUnits,
        totalUnits,
        adjustedTotalUnits,
        roundedAdjustedTotalUnits
      })
      localStorage.setItem('records', JSON.stringify(state, null, 2))
    },
    recordNoInsulin(state, action) {
      const {
        consumedCarbs,
        currentBg,
        unitAdjustmentPercentage,
        minimumUnits,
        carbsPerUnit,
        bgPerUnit,
        targetBg,
        unitAdjustmentRatio,
        bgDistance
      } = action.payload
      state.unshift({
        date: Date.now(),
        consumedCarbs,
        currentBg,
        unitAdjustmentPercentage,
        minimumUnits,
        carbsPerUnit,
        bgPerUnit,
        targetBg,
        unitAdjustmentRatio,
        bgDistance,
        calculationType: null,
        bgUnits: 0,
        carbUnits: 0,
        totalUnits: 0,
        adjustedTotalUnits: 0,
        roundedAdjustedTotalUnits: 0
      })
      localStorage.setItem('records', JSON.stringify(state, null, 2))
    },
    resetRecords(state) {
      state.length = 0
      localStorage.setItem('records', JSON.stringify(state, null, 2))
    },
    removeRecordIndex(state, action) {
      state.splice(action.payload, 1)
      localStorage.setItem('records', JSON.stringify(state, null, 2))
    }
  }
})

export const {
  recordTakeInsulin,
  recordNoInsulin,
  resetRecords,
  removeRecordIndex
} = recordsSlice.actions

export default recordsSlice.reducer
